import styled from "styled-components";
import { GreyLink, Button } from "../components/Button";
import { IconInput } from "../components/Input";
import { ImageDiv } from "../components/Image";
import { MiddleText } from "../components/Text";
import { TextModal, FormModal } from "../components/Modal";
import logo from "../Image/logo.png";
import { Page, PageContainer } from "../components/Page";
import {
  forgetPassword,
  resetPassword,
  register,
  registerSuccess,
  resetSuccess,
  alertText,
} from "../constants/variable";
import { TOKEN_NAME } from "../constants/variable";
import { useState } from "react";

const MaxPage = styled(Page)`
  height: 100vh;
`;
const LoginPageContainer = styled(PageContainer)`
  display: flex;
  align-items: center;
  position: relative;
  ${({ theme }) => theme.media.sm} {
    flex-direction: column-reverse;
  }
`;
const LoginImage = styled(ImageDiv)`
  ${({ theme }) => theme.media.sm} {
    width: 20px;
    margin-bottom: ${({ theme }) => theme.space.md}px;
  }
`;
const LoginForm = styled.div`
  flex: 1 0;
  padding: ${({ theme }) => theme.space.lg}px;
  text-align: center;
  & > * ~ * {
    margin-top: ${({ theme }) => theme.space.md}px;
  }
  ${({ theme }) => theme.media.sm} {
    flex-grow: 0;
  }
`;
const DashLine = styled.hr`
  border-top: 1px dotted ${({ theme }) => theme.color.primary};
`;

const initIsModal = {
  forgetPassword: false,
  register: false,
  registerSuccess: false,
  resetSuccess: false,
};

export default function LoginPage() {
  const [isModal, setIsModal] = useState(initIsModal);
  const handleOpenModal = ({ target }) => {
    setIsModal({ ...isModal, [target.value]: true });
  };
  const handleCloseModal = ({ target }) => {
    setIsModal(initIsModal);
  };
  return (
    <MaxPage>
      <LoginPageContainer>
        <LoginImage>
          <img src={logo} alt="想像朋友寫作會"></img>
        </LoginImage>
        <LoginForm>
          <IconInput type={"text"} placeholder={"你的帳戶"} icon={"username"} />
          <IconInput
            type={"text"}
            placeholder={"你的密碼"}
            icon={"password"}
            alert={true ? alertText.usernamePasswordIncorrect : ""}
          />
          <GreyLink value="forgetPassword" onClick={handleOpenModal}>
            忘記密碼
          </GreyLink>
          <Button>登入</Button>
          <DashLine />
          <MiddleText>還沒有帳號嗎？</MiddleText>
          <Button value="register" onClick={handleOpenModal}>
            註冊
          </Button>
        </LoginForm>
      </LoginPageContainer>
      {isModal.register && (
        <FormModal content={register} handleCloseModal={handleCloseModal} />
      )}
      {isModal.forgetPassword && (
        <FormModal
          content={forgetPassword}
          handleCloseModal={handleCloseModal}
        />
      )}
      {isModal.registerSuccess && (
        <TextModal
          content={registerSuccess}
          handleCloseModal={handleCloseModal}
        />
      )}
      {isModal.resetSuccess && (
        <TextModal content={resetSuccess} handleCloseModal={handleCloseModal} />
      )}
    </MaxPage>
  );
}
